<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 公告
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button
                        type="primary"
                        icon="el-icon-delete"
                        class="handle-del mr10"
                        @click="handleAdd"
                >添加</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
            >
                <!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="Title" label="标题"></el-table-column>
                <el-table-column label="标题图片">

                    <template slot-scope="scope">
                        <el-image  style="width: 200px; height: 100px"  :src="scope.row.TitleImgUrl" lazy></el-image>
                    </template>
                </el-table-column>
                <el-table-column prop="Des" label="概要"></el-table-column>

                <el-table-column prop="State" label="状态"></el-table-column>

                <el-table-column label="操作" width="140" align="center">
                    <template slot-scope="scope">
                        <el-button
                                type="text"
                                icon="el-icon-edit"
                                @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                                type="text"
                                icon="el-icon-delete"
                                class="red"
                                @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total, prev, pager, next"
                        :current-page="query.pageIndex"
                        :page-size="query.pageSize"
                        :total="pageTotal"
                        @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </div>


        <!-- 编辑弹出框 -->
        <el-dialog  title="编辑" :visible.sync="editVisible" width="90%">
            <el-form ref="form" :model="form" :rules="rules" label-width="100px">
                <el-form-item label="标题">
                    <el-input v-model="form.Title"></el-input>
                </el-form-item>
                <el-form-item label="概要">
                    <el-input v-model="form.Des"></el-input>
                </el-form-item>
                <el-form-item label="标题图片" prop="TitleImgUrl">
                    <!--                    <el-input v-model="form.GoodsPicUrl" readonly></el-input>-->
                    <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :auto-upload="false"
                            :on-change="handleChangeGoodsPicUrl"
                            :show-file-list="false" >
                        <img v-if="form.TitleImgUrl" :src="form.TitleImgUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.TitleImgUrl" @click="delGoodsPicUrl()"  >删除</button>

                </el-form-item>
                <el-form-item label="文本框">
                    <div>
                        <tinymce v-model="form.HtmlContentFile" :height="300" />
                    </div>
                </el-form-item>

            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import VueCropper  from 'vue-cropperjs';
    import { request } from '../../utils/http';
    import Tinymce from '../../components/Tinymce'

    // 工具栏配置
    const toolbarOptions = [
        ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
        ['blockquote', 'code-block'],

        [{'header': 1}, {'header': 2}],               // custom button values
        [{'list': 'ordered'}, {'list': 'bullet'}],
        [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
        [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
        [{'direction': 'rtl'}],                         // text direction

        [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
        [{'header': [1, 2, 3, 4, 5, 6, false]}],

        [{'color': []}, {'background': []}],          // dropdown with defaults from theme
        [{'font': []}],
        [{'align': []}],
        ['link', 'image', 'video'],
        ['clean']                                         // remove formatting button
    ]

    export default {
        name: 'NoticeList',
        props: {
            //传入一个value，使组件支持v-model绑定
            value: {
                type: String,
                default: ''
            },
            disabled: {
                type: Boolean,
                default: false
            },
            plugins: {
                type: [String, Array],
                default: 'lists image media table textcolor wordcount contextmenu'
            },
            toolbar: {
                type: [String, Array],
                default: 'undo redo |  formatselect | bold italic | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | lists image media table | removeformat'
            }
        },
        data() {
            return {
                init: {
                    language_url: '/static/tinymce/langs/zh_CN.js',//语言包的路径
                    language: 'zh_CN',//语言
                    skin_url: '/static/tinymce/skins/lightgray',//skin路径
                    height: 300,//编辑器高度
                    branding: false,//是否禁用“Powered by TinyMCE”
                    menubar: false,//顶部菜单栏显示
                },
                editorOption: {
                    modules: {
                        toolbar: {
                            container: toolbarOptions,  // 工具栏
                            // handlers: {
                            //     'image': function (value) {
                            //         if (value) {
                            //             document.querySelector('.ivu-btn').click()
                            //         } else {
                            //             this.quill.format('image', false);
                            //         }
                            //     }
                            // }
                        }
                    },
                    theme: 'snow',
                },
                defaultSrc: require('../../assets/img/img.jpg'),
                fileList: [],
                imgSrc: '',
                cropImg: '',
                dialogVisible: false,
                query: {
                    state: 0,
                    goodsName: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                rules: {

                },

                tableData: [],
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                idx: -1,
                id: -1,
                TitleImgUrlFile:{},
            };
        },
        created() {
            this.getData();
        },
        mounted() {
            // this.editor = this.$refs.myQuillEditor.quill;

        },
        beforeDestroy(){
            // this.editor = null;
            // delete this.editor;
        },
        components: {
            VueCropper,
            Tinymce
        },
        methods: {
            handleChangeGoodsPicUrl(file, fileList) {
                this.form.TitleImgUrl = URL.createObjectURL(file.raw);
                this.TitleImgUrlFile = file.raw;
            },


            // 获取 easy-mock 的模拟数据
            getData() {
                request({ url:'filmbll/getnoticelist', method:'post', data:this.query }).then((res)=>{
                    if (res.code==200){
                        this.tableData = res.obj.ListData;
                        this.pageTotal = res.obj.PageTotal;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.$set(this.query, 'pageIndex', 1);
                this.getData();
            },
            // 删除操作
            handleDelete(index, row) {
                // 二次确认删除
                this.$confirm('确定要删除吗？', '提示', {
                    type: 'warning'
                })
                    .then(() => {
                        var data = {
                            Id:row.Id
                        }
                        request({
                            url:'filmbll/delarticle', method:'post',
                            data:data
                        }).then((res)=>{
                            console.log(res);
                            if (res.code==200){
                                this.$message.success('删除成功');
                                this.tableData.splice(index, 1);
                                this.getData();
                            } else {
                                this.$message.error('删除失败：'+res.msg);
                            }
                        });

                    })
                    .catch(() => {});
            },
            // 编辑操作
            handleEdit(index, row) {
                var aData={
                    id:row.Id
                }
                request({
                    url:'filmbll/getarticle',  method:'post',
                    data:aData
                }).then((res)=>{
                    console.log(res);
                    if (res.code==200){
                        this.idx = index;
                        this.form = res.obj;
                        this.editVisible = true;

                    } else {
                        this.$message.error('数据变动，无法编辑：'+res.msg);
                    }
                });
            },
            handleAdd() {
                this.idx =-1;
                var aFilm ={

                }
                this.form = aFilm;
                this.form.ArticleType = 1;
                this.form.State = 2;

                this.editVisible = true;
            },
            // 保存编辑
            saveEdit() {
                this.form.TitleImgUrlFile = this.TitleImgUrlFile;
                if (this.idx===-1) {
                    this.form.Id = 0
                }
                request({
                    url:'filmbll/savearticlehaspic', method:'file',
                    data:this.form
                }).then((res)=>{
                    console.log(res);
                    if (res.code==200){
                        this.editVisible = false;
                        this.$message.success(`保存成功`);
                        this.getData();
                    } else {
                        this.$message.error('保存失败：'+res.msg);
                    }
                });
            },
            // 分页导航
            handlePageChange(val) {
                this.$set(this.query, 'pageIndex', val);
                this.getData();
            }
        }
    };
</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
    .ivu-upload {
        display: none;
    }
    .ivu-btn{

    }
</style>
